.auth {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;

    &__top {
        text-align: center;
        margin-bottom: 50px;
        h2 {
          font-size: 20px; 
          margin-bottom: 5px;
        }
        p {
            font-size: 18px;
            line-height: 18px;
            letter-spacing: 0.1px;
            opacity: 0.5;
        }
    }

    &__register-link {
        text-align: center;
        display: block;
        color: #ADADAD;
        letter-spacing: 0.1px;
        font-size: 16px;
    }

    &__success-block{
        text-align: center;
        height: 400px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        h2 {
            margin-top: 10px;
        }
        p {
            color: #ADADAD;
        }
    }
    
    .button {
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 0.1px;
        width: 100%;
    }

    .block {
        width: 430px;
    }
}