@import "~styles/_variables.scss";

.message {
    $self: &;

    &__content {
        display: flex;
        max-width: 440px;
        margin-bottom: 20px;
        position: relative;
    }

    &--is-audio {
        audio {
            display: none;
        }
        #{$self}__bubble {
            position: relative;
            width: 270px;
            overflow: hidden;
        }
        #{$self}__audio {
            padding: 15px;
            background-color: #3674ff;
            border-radius: 6px 6px 0 6px;
            width: 250px;
            overflow: hidden;
            position: relative;
            &-info {
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: relative;
            }
            &-btn {
                button {
                    border: 0;
                    width: 28px;
                    height: 28px;
                    border-radius: 30px;
                    background: #0f3997;
                    text-align: center;
                    font-size: 8px;
                    cursor: pointer;
                }
            }
            &-duration {
                color: #ffffff;
                font-weight: 500;
                opacity: 0.5;
                font-size: 12px;
            }
            &-progress {
                left: 0;
                top: 0;
                position: absolute;
                background-color: #418fff;
                height: 100%;
                transition: width 0.5s ease-out;
            }
        }
    }

    &__avatar {
        display: flex;
        flex-direction: column-reverse;
        .avatar {
            border-radius: 50px;
            width: 33px;
            height: 33px;
            margin-right: 13px;
            margin-bottom: 5px;
            font-size: 14px;
            line-height: 31px;
        }
    }

    &__bubble {
        background: $blue;
        box-shadow: 0px 5px 5px rgba(54, 116, 255, 0.196773);
        border-radius: 12px 12px 12px 0px;
        padding: 15px;
        margin-bottom: 8px;
        order: 2;
    }

    &__date {
        font-size: 12px;
        opacity: 0.5;
    }

    &__text {
        color: #ffffff;
        line-height: 20px;
    }

    &__icon {
        &-readed {
            position: absolute;
            bottom: 25px;
            left: -25px;
        }

        &-actions {
            display: none;
            transform: rotate(90deg);
            position: absolute;
            left: -31px;
            top: -5px;
        }
    }

    &__attachments {
        display: flex;
        margin-bottom: 5px;
        order: 1;
        &-item {
            margin-right: 10px;
            cursor: pointer;
            img {
                width: 45px;
                height: 45px;
                border-radius: 8px;
            }
        }
    }

    &--isme {
        display: flex;
        flex-direction: row-reverse;

        #{$self}__content {
            flex-direction: row-reverse;
            &:hover {
                #{$self}__icon-actions {
                    display: block;
                }
            }
        }

        #{$self}__avatar {
            margin-left: 13px;
            img {
                margin-right: 0;
                margin-left: 13px;
            }
        }

        #{$self}__info {
            display: flex;
            align-items: center;
            text-align: right;
        }

        #{$self}__bubble {
            background: #ffffff;
            border: 1px solid #ececec;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.220444);
            border-radius: 12px 12px 0px 12px;
            order: 2;
            #{$self}__text {
                color: $black;
            }
        }
    }

    &--image {
        #{$self}__attachments {
            &-item {
                img {
                    width: 150px;
                    height: 150px;
                }
            }
        }
    }

    &__typing {
        span {
            height: 8px;
            width: 8px;
            border-radius: 50%;
            background: $message-typing-circle;
            display: inline-block;
            margin-right: 8px;
            animation: 0.8s typingAnimation infinite ease-out 0.1s;

            &:nth-of-type(2) {
                animation: 0.8s typingAnimation infinite ease-out 0.2s;
            }
            &:nth-of-type(3) {
                animation: 0.8s typingAnimation infinite ease-out 0.3s;
                margin-right: 0;
            }
        }
    }
}

@keyframes typingAnimation {
    from {
        opacity: 0.2;
        transform: translateY(0px);
    }
    50% {
        opacity: 0.5;
        transform: translateY(-1.5px);
    }
    to {
        opacity: 1;
        transform: translateY(-1px);
    }
}
